@charset "utf-8";

/*fonts*/
@font-face {
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 300;
    src: url('../fonts/NotoSansKR-DemiLight-Hestia.eot');
    src: url('../fonts/NotoSansKR-DemiLight-Hestia.eot?') format('eot'), url('../fonts/NotoSansKR-DemiLight-Hestia.woff') format('woff');
}

@font-face {
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 600;
    src: url('../fonts/NotoSansKR-Bold-Hestia.eot');
    src: url('../fonts/NotoSansKR-Bold-Hestia.eot?') format('eot'), url('../fonts/NotoSansKR-Bold-Hestia.woff') format('woff');
}

/* Common */
*,
*:before,
*:after {
    -webkit-box-sizing: border-box;
    box-sizing: border-box
}

html,
body,
div,
span,
applet,
object,
iform-area,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    font-family: 'Noto Sans KR', '맑은 고딕', sans-serif;
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    vertical-align: baseline;
    background: transparent;
    font-size: 14px;
    letter-spacing: -0.05em;
    line-height: 1.5;
    color: #333;
}

body {
    line-height: 1;
}

html,
body {
    min-height: 100%;
}

ul {
    list-style: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    table-layout: fixed;
}

td,
th {
    border-collapse: collapse;
    border-spacing: 0;
    word-wrap: break-word;
    word-break: break-all;
}

caption {
    overflow: hidden;
    width: 1px;
    height: 1px;
    margin: -1px 0px;
    font-size: 1px;
    line-height: 0;
}

a {
    display: block;
    text-decoration: none;
}

img,
fieldset,
button,
input,
select,
textarea {
    border: 0 none;
    -moz-appearance: none;
    appearance: none;
    -webkit-appearance: none;
}

img {
    max-width: 100%;
    display: block;
}

input {
    border: 0;
    background: #fff
}

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
    color: #999 !important;
}

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
    color: #999 !important;
}

img,
input,
select,
button,
label {
    vertical-align: top;
    font-size: 16px;
}

input[disabled="disabled"] {
    background: #f8f8f8 !important
}

button:disabled,
button[disabled] {
    background: #ddd !important;
    color: #aaa !important;
    cursor: default !important
}

h1 {
    font-size: 27px;
}

.hidden,
.blind {
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    width: 1px;
    height: 1px;
    font-size: 0;
    line-height: 0
}

.margin0 {
    margin-top: 0px !important;
}

.margin10 {
    margin-top: 10px !important;
}

/*style*/
#wrap {
    -webkit-text-size-adjust: 100%;
    min-width: 320px;
    max-width: 648px;
    margin: 0 auto;
}

.container {
