}

/*checkbox style*/
.event-wrap .chk-warp [type="checkbox"]:not(:checked),
.event-wrap .chk-warp [type="checkbox"]:checked {
    position: absolute;
    left: -9999px;
}

.event-wrap .chk-warp [type="checkbox"]:not(:checked)+label,
.event-wrap .chk-warp [type="checkbox"]:checked+label {
    position: relative;
    padding-left: 30px;
    cursor: pointer;
    color: #4a4a4a;
    letter-spacing: -0.05em;
}

.event-wrap .chk-warp [type="checkbox"]:not(:checked)+label:before,
.event-wrap .chk-warp [type="checkbox"]:checked+label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 1px;
    width: 23px;
    height: 23px;
    background: url('../img/event_2019/galaxy_s20/chk-icon.png') no-repeat -22px 0;
}

.event-wrap .chk-warp [type="checkbox"]:not(:checked)+label:after,
.event-wrap .chk-warp [type="checkbox"]:checked+label:after {
    content: '';
    position: absolute;
    left: 0;
    top: 1px;
    width: 23px;
    height: 23px;
    -webkit-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
    background: url('../img/event_2019/galaxy_s20/chk-icon.png') no-repeat;
}

.event-wrap .chk-warp [type="checkbox"]:not(:checked)+label:after {
    opacity: 0;
}

.event-wrap .chk-warp [type="checkbox"]:checked+label:after {
    opacity: 1;
}

.event-wrap .chk-warp [type="checkbox"]+label {
    display: block;
}

.event-wrap .chk-warp [type="checkbox"]+label span {
    font-size: 16px;
    color: #aaa;
}

.event-wrap .chk-warp [type="checkbox"]+label span.required {
    font-size: 16px;
    color: #f70000;
}

/* field-set */
.field-set {
    position: relative;
    margin-top: 0;
}

.field-set:first-child {
    margin-top: 15px;
}

.field-set .form-set {
    position: relative;
    color: #000;
}

.field-set .form-set:focus {
    outline: 1px solid #4a4a4a;
    outline-offset: -1px;
    z-index: 100;
}

.field-set.check-type {
    padding: 0;
}

/* radio */
.radio-set {
    position: relative;
    display: inline-block;
    margin: 0 40px 10px 0;
}

.radio-set .label {
    position: relative;
    display: inline-block;
    padding-left: 38px;
    line-height: 30px;
    font-size: 16px;
    cursor: pointer;
}

.radio-set .label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 30px;
    height: 30px;
    background-size: contain;
    background-image: url('../img/event_2019/galaxy_s20/icon-radio.png');
    background-repeat: no-repeat;
}

.radio-set input[type="radio"],
.row-layout.pic-radio-type.type2 input[type="radio"] {
    position: absolute;
    left: 0;
    top: 0;
    width: 1px;
    height: 1px;
    visibility: hidden;
}

.radio-set input[type="radio"]:checked+.label:before,
.row-layout.pic-radio-type.type2 input[type="radio"]:checked+.label:before {
    background-image: url('../img/event_2019/galaxy_s20/icon-radio-checked.png');
}

.radio-set.pic-radio {
    margin: 0;
    display: block;
}

.radio-set.pic-radio p {
    padding: 0
}

.radio-set.pic-radio .pic-tit {
    font-size: 14px;
    line-height: 1;
    color: #595959;
}

.radio-set.pic-radio .pic-desc {
    font-size: 14px;
    line-height: 1;
    color: #3b98e6;
    margin-top: 3px;
}

.radio-set.pic-radio .pic-desc .check-point {
    color: inherit
}

.radio-set.pic-radio .label {
    display: block;
    padding: 0;
    line-height: 1;
}

.radio-set.pic-radio .label:before {
    display: none;
}

.radio-set.pic-radio .label:after {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    margin-top: 5px;
    transform: translateX(-50%);
    width: 21px;
    height: 21px;
    background-image: url('../img/event_2019/galaxy_s20/icon-radio-type2.png');
    background-size: contain
}

.radio-set.pic-radio input[type="radio"]:checked+.label:before {
    content: '';
    display: block;
    margin-top: 5px;
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    width: 21px;
    height: 21px;
    background-image: url('../img/event_2019/galaxy_s20/icon-radio-type2-checked.png');
    background-size: contain
}

.radio-set.pic-radio input[type="radio"]:checked+.label:after {
    display: none;
}

.radio-set.pic-radio .pic {
    display: inline-block;
    width: 100%;
    padding-bottom: 65%;
}

.radio-set.pic-radio .pic.spot1 {
    background: url('../img/event_2019/galaxy_s20/tour-spot1.jpg') center top no-repeat;
    background-size: contain
}

.radio-set.pic-radio .pic.spot2 {
    background: url('../img/event_2019/galaxy_s20/tour-spot2.jpg') center top no-repeat;
    background-size: contain
}

.radio-set.pic-radio .pic.spot3 {
    background: url('../img/event_2019/galaxy_s20/tour-spot3.jpg') center top no-repeat;
    background-size: contain
}

.radio-set.pic-radio .pic.spot4 {
