/* select */
.field-set select.form-set {
    width: 100%;
    padding-left: 18px;
    height: 50px;
    line-height: 50px;
    font-family: inherit;
    border-radius: 0px;
    ms-appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: #f8f8f8 url('../img/event_2019/galaxy_s20/icon-select-arrow.jpg') no-repeat 95% 50%;
    ;
}

.field-set select.form-set::-ms-expand {
    display: none;
}

.field-set select.form-set:focus::-ms-value {
    background-color: transparent !important;
    color: #000;
}

.field-set select.form-set option:hover {
    background: #3f94dd;
    color: #fff;
}

.field-set select.form-set option:checked {
    background: transparent;
    color: #000;
}

.field-set.btn-type .form-set {
    width: 70% !important;
    margin-top: 0 !important;
}

.field-set.btn-type .count-down-area {
    position: absolute;
    top: 50%;
    right: 35%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}

.field-set.btn-type .count-down-area,
.field-set.btn-type .count-down-area span {
    font-size: 15px;
    color: #f5202f;
}

.field-set.btn-type button {
    position: absolute;
    bottom: 0;
    right: 0;
    height: 50px;
    width: 30%;
    background: #434159;
    color: #fff;
    cursor: pointer;
    word-break: keep-all;
    font-size: 15px;
}

.check-type .check-box+.form-set {
    margin-top: 12px;
}

.check-type {
    margin-top: 15px;
}

.check-type+.check-type {
    margin-top: 26px;
}

.field-set+.field-set {
    margin-top: 15px;
}

/* rate */
.field-set.rate .rating-area {
    font-size: 0;
    display: inline-block;
    margin: 0 auto;
}

.field-set.rate .rating-area>img {
    display: inline-block;
    width: 100%;
    max-width: 74px;
}

.field-set.rate .rating-area>img+img {
