/* hash-tag area */
.hash-area {
    text-align: center;
    margin-top: 14px;
    padding: 0 20px 50px;
    word-break: keep-all
}

.hash-area .hash-tit {
    display: block;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 23px;
}

.hash-area .hash-badge {
    display: inline-block;
    padding: 2px 18px;
    border-radius: 20px;
    background: #224332;
    margin: 0;
    color: #fff;
    font-size: 22px;
}

.hash-area .hash-badge>span {
    vertical-align: 4px;
    color: inherit;
    font-size: inherit
}

.hash-area .hash-box {
    border: 1px solid #224332;
    max-width: 430px;
    margin: 0 auto;
    padding: 34px 10px 20px;
